#mainDivAdministrator {
  padding: 100px 20px;
  background: rgb(130, 175, 109);
  background: radial-gradient(circle, rgb(130, 175, 109) 3%, rgb(226, 193, 115) 100%);
  height: 100vh;
  width: 100vw;
  display: grid;
  overflow-x: hidden;
  grid-template-areas: "nav nav nav" "data data input" "data data input";
  grid-template-columns: 1fr 1fr 1fr;
}
#mainDivAdministrator #divForNavigation {
  grid-area: nav;
}
#mainDivAdministrator #divForDataFromBase {
  grid-area: data;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
}
#mainDivAdministrator #divForDataFromBase #divForSelectAndBtn {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 30px;
  width: 100%;
}
#mainDivAdministrator #divForDataFromBase #divForSelectAndBtn select {
  padding: 5px 15px;
}
#mainDivAdministrator #divForDataFromBase #divForSelectAndBtn button {
  padding: 5px 15px;
}
#mainDivAdministrator #divForDataFromBase #divForData {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 50px;
  width: 50%;
  padding: 30px 5px;
}
#mainDivAdministrator #divForDataFromBase #divForData .oneUser,
#mainDivAdministrator #divForDataFromBase #divForData .onePodcastAdministratorPage {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  border: 1px solid #333;
  padding: 10px;
}
#mainDivAdministrator #divForInputPodcast {
  grid-area: input;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 30px 0px;
  gap: 40px;
  height: 500px;
}
#mainDivAdministrator #divForInputPodcast .oneInputAdministratorPage {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
#mainDivAdministrator #divForInputPodcast .oneInputAdministratorPage input {
  width: 60%;
  height: 150%;
  padding-left: 10px;
}
#mainDivAdministrator #divForInputPodcast .oneInputAdministratorPage label {
  width: 30%;
  text-align: center;
}
#mainDivAdministrator #divForInputPodcast #btnSubmit {
  width: 30%;
  height: 30px;
}

.buttonsOneUser {
  display: flex;
  gap: 20px;
}

.buttonsOneUser button,
.deletePodcastBtn {
  padding: 5px 15px;
  transition: 0.2s;
}
.buttonsOneUser button:hover,
.deletePodcastBtn:hover {
  background-color: #333;
  color: whitesmoke;
}

@media screen and (max-width: 1300px) {
  #mainDivAdministrator {
    padding-top: 150px;
  }
  #mainDivAdministrator #divForDataFromBase {
    grid-area: data;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 50px;
  }
  #mainDivAdministrator #divForDataFromBase #divForSelectAndBtn {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 30px;
    width: 100%;
  }
  #mainDivAdministrator #divForDataFromBase #divForData {
    width: 100%;
  }
  #mainDivAdministrator #divForDataFromBase #divForData .oneUser,
#mainDivAdministrator #divForDataFromBase #divForData .onePodcastAdministratorPage {
    width: 80%;
    gap: 30px;
    border: 2px solid black;
    padding: 20px;
  }
  #mainDivAdministrator #divForInputPodcast {
    width: 40vw;
  }
  #mainDivAdministrator #divForInputPodcast .oneInputAdministratorPage {
    width: 100%;
  }
}
@media screen and (max-width: 800px) {
  #mainDivAdministrator {
    background-size: contain;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  #mainDivAdministrator #divForDataFromBase {
    width: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 30px;
  }
  #mainDivAdministrator #divForDataFromBase #divForData {
    gap: 50px;
    width: 90%;
  }
  #mainDivAdministrator #divForDataFromBase #divForData .oneUser,
#mainDivAdministrator #divForDataFromBase #divForData .onePodcastAdministratorPage {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    border: 1px solid #333;
    padding: 10px;
  }
  #mainDivAdministrator #divForInputPodcast {
    width: 90%;
    padding: 30px 0px;
  }
  #mainDivAdministrator #divForInputPodcast .oneInputAdministratorPage {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  #mainDivAdministrator #divForInputPodcast .oneInputAdministratorPage label {
    font-size: 1.3rem;
  }
}
@media screen and (max-width: 500px) {
  #mainDivAdministrator {
    background-size: contain;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  #mainDivAdministrator #divForDataFromBase {
    width: 100vw;
    padding: 0px 5px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 30px;
  }
  #mainDivAdministrator #divForDataFromBase #divForData {
    gap: 50px;
    width: 100%;
  }
  #mainDivAdministrator #divForDataFromBase #divForData .oneUser,
#mainDivAdministrator #divForDataFromBase #divForData .onePodcastAdministratorPage {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    border: 1px solid #333;
    padding: 5px;
    text-align: center;
  }
  #mainDivAdministrator #divForDataFromBase #divForData .onePodcastAdministratorPage h4 {
    font-size: 0.8rem;
  }
  #mainDivAdministrator #divForInputPodcast {
    width: 100vw;
    padding: 30px 0px;
  }
  #mainDivAdministrator #divForInputPodcast P {
    font-size: 1.5rem;
    font-weight: bold;
  }
  #mainDivAdministrator #divForInputPodcast .oneInputAdministratorPage {
    width: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  #mainDivAdministrator #divForInputPodcast .oneInputAdministratorPage label {
    font-size: 1.3rem;
  }
}/*# sourceMappingURL=Administrator.css.map */