@import "../../../Mixin/Mixin.scss";

#mainDivAccountPage {
  @include flex-centar-column();
  background-image: url("/public/images/2.png");
  gap: 200px;
  width: 100vw;
  height: 100vh;
  padding: 100px 20px;
  #dataOfUser {
    @include flex-centar-column();
    gap: 40px;
    width: 50vw;
    height: 50vh;
    P {
      font-size: 1.5rem;
      font-weight: bold;
      text-align: center;
    }
    .inputAccountPage {
      @include flex-centar-column();
      gap: 10px;
      width: 80%;
      label {
        font-weight: 600;
        font-size: 1.2rem;
      }
      input {
        width: 400px;
        height: 30px;
        padding: 10px;
        transition: 0.3s;
        &:focus {
          transform: scale(1.2);
        }
      }
    }
    #saveChanges {
      padding: 10px 40px;
      transition: 0.2s;
      border-radius: 5px;
      border: none;
      &:hover {
        background-color: #333;
        color: whitesmoke;
      }
    }
  }
  #btnForLoginDelete {
    @include flex-centar-row();
    gap: 100px;
    width: 100%;
    #logoutBtn {
      width: 250px;
      padding: 20px 40px;
      border: none;
      border-radius: 5px;
      transition: 0.2s;
      &:hover {
        transform: scale(1.2);
      }
    }
    #deleteAccoutBtn {
      width: 250px;
      background-color: red;
      color: white;
      padding: 20px 40px;
      border: none;
      border-radius: 5px;
      transition: 0.2s;
      &:hover {
        transform: scale(1.2);
      }
    }
  }
}

@media screen and (max-width: 1540px) {
  #mainDivAccountPage {
    gap: 50px;
    background-size: cover;
    #dataOfUser {
      @include flex-centar-column();
      gap: 20px;
      P {
        font-size: 1.4rem;
      }
    }
    #btnForLoginDelete {
      @include flex-centar-row();
      gap: 100px;
      width: 100%;
      #logoutBtn {
        width: 200px;
        padding: 10px 40px;
        border: none;
        border-radius: 5px;
        transition: 0.2s;
        &:hover {
          transform: scale(1.2);
        }
      }
      #deleteAccoutBtn {
        width: 200px;
        padding: 10px 30px;
      }
    }
  }
}

@media screen and (max-width: 500px) {
  #mainDivAccountPage {
    padding-top: 200px;
    #dataOfUser {
      @include flex-centar-column();
      gap: 20px;
      width: 80vw;
      P {
        width: 90vw;
        font-size: 1.2rem;
      }
      .inputAccountPage {
        @include flex-centar-column();
        gap: 10px;
        width: 80%;
        input {
          width: 300px;
        }
      }
    }
    #btnForLoginDelete {
    padding-top: 50px;
      @include flex-centar-column();
      gap: 30px;
      width: 100%;
      #logoutBtn {
        width: 250px;
      }
      #deleteAccoutBtn {
        width: 250px;
        background-color: red;
      }
    }
  }
}
